import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class CovidapiService {
  private globalDataUrl = 'https://covid19.mathdro.id/api'
  constructor(private http: HttpClient) { }

  getAllData(){
    return this.http.get(this.globalDataUrl);
  }

  getDataByCountry(country: string){
    return this.http.get(this.globalDataUrl + '/countries/' + country);
  }

  getDailyData(){
    return this.http.get(this.globalDataUrl + '/daily');
  }
  getCountries(){
    return this.http.get(this.globalDataUrl + '/countries');
  }


}
