import { Component, OnInit } from '@angular/core';
import { CovidapiService } from '../services/covidapi.service';
import { GlobalModel } from '../models/global.model';


@Component({
  selector: 'app-indetailes',
  templateUrl: './indetailes.component.html',
  styleUrls: ['./indetailes.component.scss']
})
export class IndetailesComponent implements OnInit {

  constructor(private covidService: CovidapiService) {
    this.data = new GlobalModel();
  }
  globale: boolean;
  country: string;
  data: GlobalModel;
  dailyData: any[];
  countries: any[];
  lineChartData: any[] = [
    {
      data: [65,64,33,44], label:'temp label'
    }
  ];
  lineChartType = 'line';
  lineCharLabels: any[] = ['label1', 'label2', 'label3'];
  barChartData: any[] = [
    {
      data: [65,64,33,44], label:' label'
    }
  ];
  barChartType = 'bar';
  barChartLabels: any[] = ['confirmed', 'recovered','deaths'];
  ngOnInit() {
    this.globale = true;
    this.getAllData();
    this.getCountries();
    this.getDailyData();
  }

  getAllData(){
    this.covidService.getAllData().subscribe((res: any[]) => {
      this.data.confirmed = res['confirmed']['value'];
      this.data.deaths = res['deaths']['value'];
      this.data.recovered = res['recovered']['value'];
      this.data.lastupdate = res['lastUpdate'];
    })
    console.log(this.data);
  }

  getCountries(){
    this.covidService.getCountries().subscribe((res: any[]) => {
    var countries = res['countries'];
    this.countries = countries.map(name => name['name'])
    })
  }

  getDataByCountry(country: string){
     this.covidService.getDataByCountry(country).subscribe((res: any[]) =>{
      this.data.confirmed = res['confirmed']['value'];
      this.data.deaths = res['deaths']['value'];
      this.data.recovered = res['recovered']['value'];
      this.data.lastupdate = res['lastUpdate'];
      //console.log(this.data);
      this.barChartData = [
        {
          data: [this.data.confirmed, this.data.recovered, this.data.deaths],
          label: "People"
        }
      ]
     })
  }


  getDailyData(){
    this.covidService.getDailyData().subscribe((res: any[])=>{
      this.lineCharLabels = res.map((date)=>date['reportDate']);
      var interfaceData = res.map((confirmed)=>confirmed['totalConfirmed']);
      var deaths = res.map((deaths)=>deaths['deaths']['total']);
      var recovered = res.map((rev)=>{rev});
      this.lineChartData = [
        {
          data: interfaceData,
          label: 'Interfaced'
        },
        {
          data: deaths,
          label: 'Deaths'
        }
      ]
     // console.log(this.lineCharLabels,this.lineChartData);
    })
  }

  countryChanged(value: string){
    this.country = value;
    if(value == 'global'){
      this.getAllData();
      this.globale = true;
    } else {
      this.getDataByCountry(value);
      this.globale = false;
    }
  }


}
